@use '../../foundation/mixin' as mixin ;

.p-header  {   
    &__pc  {
        width: 920px;
        display: flex;
        justify-content: space-between;
        align-items: center;
            @include mixin.smartphone {
                display: none;
            }
    }

    &__list {
        width: 670px;
        display: flex;
        justify-content: space-between;

    }

    &__link {
        @include mixin.verticalcenter
    }

    &__mobile {
        width: 300px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        @include mixin.pc {
            display: none;
        }

        transition: all .4s;/*アニメーションの設定*/

        &.is-open {
            background-color: white;
        }
      
      
  }


}